@use "../../../styles/colors" as colors;

.meal {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(colors.$secondary, 0.75);

  h3 {
    margin: 0 0 0.25rem 0;
  }

  .description {
    font-style: italic;
  }
}
.price {
  margin-top: 0.25rem;
  font-weight: bold;
  color: colors.$primary;
  font-size: 1.25rem;
}
